//eslint-disable @typescript-eslint/no-use-before-define */
import * as React from 'react';
import { DataGrid } from '@material-ui/data-grid';
import {
  randomCreatedDate,
  randomTraderName,
  randomUpdatedDate,
} from '@material-ui/x-grid-data-generator';

const columns = [
    {field: '_id', headerName:'ID', editable: false},
  { field: 'title', headerName: 'Product Name', width: 180, editable: true },
  { field: 'price', headerName: 'Price', type: 'number',width: 120, editable: true },
  {
    field: 'description',
    headerName: 'Descrription',
    type: 'text',
    width: 180,
    editable: true,
  },
  {
      field: 'caregory',
      headerName: 'Category',
      type: 'text',
      editable: true
  },
  {
      field: 'image',
      headerName: 'image',
      type: 'img',
      editable: true

  },
  {
    field: 'lastLogin',
    headerName: 'Last Login',
    type: 'dateTime',
    width: 220,
    editable: true,
  },
];

const rows = [
  {
    id: 1,
    name: randomTraderName(),
    age: 25,
    dateCreated: randomCreatedDate(),
    lastLogin: randomUpdatedDate(),
  },
  {
    id: 2,
    name: randomTraderName(),
    age: 36,
    dateCreated: randomCreatedDate(),
    lastLogin: randomUpdatedDate(),
  },
  {
    id: 3,
    name: randomTraderName(),
    age: 19,
    dateCreated: randomCreatedDate(),
    lastLogin: randomUpdatedDate(),
  },
  {
    id: 4,
    name: randomTraderName(),
    age: 28,
    dateCreated: randomCreatedDate(),
    lastLogin: randomUpdatedDate(),
  },
  {
    id: 5,
    name: randomTraderName(),
    age: 23,
    dateCreated: randomCreatedDate(),
    lastLogin: randomUpdatedDate(),
  },
];




function Admin(){
    return(
        <>
        <div>admin page</div>
        {/* <div style={{ height: 300, width: '200%' }}> */}
      <DataGrid rows={rows} columns={columns} autoPageSize={true}	 />
    {/* </div> */}
    </>
    );

}

export default Admin;
